.cards {
    padding: 4rem;
    background: rgb(44, 44, 44);
}

h1 {
    color: #fff;
    margin-top: -10px;
    text-align: center;
}

.cards-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
}

.cards-wrapper {
    position: relative;
    margin: 50px 0 45px;
    background: rgb(34, 34, 34);
    /* Dark background to resemble a code box */
    border-radius: 15px;
    /* Rounded corners */
    padding: 20px;
    /* Padding inside the wrapper */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* Subtle shadow for depth */
    border: 1px solid #555;
    /* Border color */
}

.cards-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 24px;
    padding: 0;
    list-style: none;
    z-index: 998;
}

.cards-item {

    background-color: rgb(189, 189, 189);
    flex: 1 1 30%;
    margin: 1rem;
    border-radius: 15px;
    width: 300px;
        /* Set a fixed width for the cards */
        max-width: calc(100% - 2rem);
        /* Limit the maximum width of the cards */
    box-sizing: border-box;
}

.cards-item-link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
}

.cards-item-pic-wrap {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 56.25%;
    /* 16:9 Aspect Ratio */
    overflow: hidden;
}

.cards-item-pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc(100% - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
}

.cards-item-img {
    position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        max-width: 100%;
        height: 100%;
        max-height: 100%;
        object-fit: cover;
        transition: all 0.2s linear;
}

.cards-item-img:hover {
    transform: scale(1.1);
}

.cards-item-info {
    padding: 20px 30px 30px;
}

.cards-item-text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
}

@media only screen and (min-width: 1200px) {
    .content-blog-container {
        width: 84%;
    }
}

@media only screen and (min-width: 1024px) {
    .cards-items {
        display: flex;
    }
}

@media only screen and (max-width: 1024px) {
    .cards-items {
        flex-direction: column;
    }

    .cards-item {
        margin-bottom: 2rem;
        flex: 1 1 auto;
    }
}